@import 'vars';


.favourite{
    width: 100%;
    margin: 20px auto 40px;
    padding: 25px 20px;
    background-color: $dark_2;
    border-radius: 6px;
    min-height: 450px;


    h3{
        text-align: center;
        font-weight: 600;
        font-size: 27px;
        padding-bottom: 40px;
    }

    &-list{
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
    }

}
