@import 'vars';


aside{
    height: 100%;
    width: 300px;
    background-color: $dark_2;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    padding: 24px;


    h5{
        font-size: 16px;
        font-weight: 600;
        padding-bottom: 32px;
    }
    .categories{
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        gap: 12px;
        color: $secondary_1;
        text-transform: capitalize;
    }
    .aside-more{
        display: flex;
        justify-content: space-between;
        color: $secondary_1;
    }
    .conditions{
        position: relative;
        &::before{
            content: '';
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 1px;
            background-color: $secondary_1;
        }
        &:hover::before{
            @extend %hover-text;
            background-color: $main-light;
        }
    }
}