@import 'vars';

.busket-active *{
    stroke: $main;
    fill: $main;
}

.busket-empthy{
    font-size: 25px;
    text-align: center;
    padding-top: 130px;
}


.busket{
    max-width: 1100px;
    width: 100%;
    margin: 40px auto;
    padding: 25px 40px;
    background-color: $dark_2;
    border-radius: 6px;
    min-height: 450px;
    display: flex;
    flex-direction: column;

    h3{
        text-align: center;
        font-weight: 600;
        font-size: 27px;
        padding-bottom: 20px;
    }

    &-items{
        display: flex;
        flex-direction: column;
        gap: 20px;
        flex-grow: 1;
        padding-bottom: 30px;
    }

    &-item{
        height: 100px;
        width: 100%;
        background-color: $dark_1;
        border-radius: 6px;
        padding: 5px 20px;

        display: flex;
        justify-content: space-between;
        align-items: center;

        img{
            width: 80px;
            height: 80px;
            border-radius: 6px;
        }
    }

    &-img{
        width: 80px;
        height: 80px;
        background-color: $secondary_2;
        border-radius: 6px;
    }

    &-product{
        display: flex;
        align-items: center;
        gap: 20px;
        width: 300px;
        font-size: 16px;
    }

    &-price{
        font-size: 18px;
    }

    &-delete__icon{
        width: 20px;
        height: 20px;

        &:hover{
            stroke: $main-light;
        }
    }

    &-couner{
        display: flex;
        align-items: center;
        gap: 20px;
        font-size: 18px;
        &-btn{
            border-radius: 6px;
            background-color: $main;
            width: 30px;
            height: 30px;
        }
        &__disabled{
            background-color: $secondary_2;
            color: $dark_2;
            border-radius: 6px;
            width: 30px;
            height: 30px;
            cursor: default;
        }
    }
}

.busket-footer{
    display: flex;
    align-items: center;
    justify-content: space-between;

    .busket-total{
        font-size: 20px;
        span{
            color: $main-light;
            font-size: 24px;
            padding-left: 15px;
        }
    }
    &__link{
        color: $secondary_2;
        position: relative;

        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            display: block;
            width: 100%;
            height: 1px;
            background-color: $secondary_2;
        }
        &:hover::after{
            background-color: $main-light;
            transition: .4s;
        }
    }
}

